<template>
  <div>
    Enter code that sent to : {{ phone }}
    <br>
    Confirm code <input type="text" v-model="confirmCode" >
    <button @click="confirm" :disabled="loading">confirm</button>
    <br>
    havent received code? 
    <span v-if="counter>0">wait {{ counter }} second</span>
    <span @click="resend()" v-else>Resend code</span>
  </div>
</template>

<script>
import AuthService from '@/services/auth.service';

export default {
  data() {
    return {
      confirmCode: '',
      loading: false,
      counter: 3
    }
  },
  props: {
    phone: {
      type: String
    }
  },
  created() {
    this.timer();
  },
  methods: {
    confirm() {
      this.loading = true;
      this.$store.dispatch('auth/confirm', {phone: this.phone, confirmCode: this.confirmCode})
        .then(()=> {
          this.$router.push({name: 'Home'});
        })
        .catch(err=> console.log(err.response))
        .then(()=> {
          this.loading = false
        });
    },
    resend() {
      AuthService.resend(this.phone)
        .then(()=> {
          this.timer();
        })
        .catch(err=> console.log(err.response));
    },
    timer() {
      this.counter = 3;
      var timerHandle = setInterval(() => {
        if (--this.counter <= 0)
          clearInterval(timerHandle);
      }, 1000);
    }
  },
  computed: {
    
  }
}
</script>